<template><undefined>
<h1>Pagination 分页</h1>
<p>采用分页的形式分隔长列表，每次只加载一个页面。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>当加载/渲染所有数据将花费很多时间时；</li>
<li>可切换页码浏览数据。</li>
</ul>
<h2 id="zu-jian-yan-shi"><a class="header-anchor" href="#zu-jian-yan-shi" aria-hidden="true">¶</a> 组件演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-pagination v-model=\&quot;value\&quot; @change=\&quot;loadPage\&quot; :total=\&quot;50\&quot;&gt;\n  &lt;/v-pagination&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    methods: {\n        loadPage(i) {\n            console.log('cb' + i);\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-pagination v-model="value" @change="loadPage" :total="50">
  </v-pagination>
</template>

</div>
