<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="pagination-props"><a class="header-anchor" href="#pagination-props" aria-hidden="true">¶</a> Pagination Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>当前页数</td>
<td>Number</td>
<td>1</td>
</tr>
<tr>
<td>total</td>
<td>数据总数</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>pageSize</td>
<td>每页条数</td>
<td>Number</td>
<td>10</td>
</tr>
<tr>
<td>showSizeChanger</td>
<td>是否可以改变 pageSize</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>pageSizeOptions</td>
<td>指定每页可以显示多少条</td>
<td>Array</td>
<td>[10, 20, 30, 40]</td>
</tr>
<tr>
<td>showQuickJumper</td>
<td>是否可以快速跳转至某页</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>当为「small」时，是小尺寸分页</td>
<td>String</td>
<td>无</td>
</tr>
<tr>
<td>simple</td>
<td>当添加该属性时，显示为简单分页</td>
<td>Object</td>
<td>无</td>
</tr>
<tr>
<td>showTotal</td>
<td>用于显示总共有多少条数据</td>
<td>Function(total, totalPages)</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3 id="pagination-events"><a class="header-anchor" href="#pagination-events" aria-hidden="true">¶</a> Pagination Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>页码改变的回调，参数是改变后的页码</td>
<td>page</td>
</tr>
<tr>
<td>sizechange</td>
<td>pageSize 变化的回调</td>
<td>current, size</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
export default {
    data: ()=> ({
        value: 1
    }),
    methods: {
        pageSizeChange(current, size) {
            console.log(current, size);
        },
        showTotal(total) {
            return `全部 ${total} 条`;
        },
        showTotal2(total,pages) {
            return `1-${pages} 全部 ${total} 条`;
        },
        loadPage(i) {
          	console.log('cb' + i);
        }
    }
}
</script>


